import type { AxiosPromise } from 'axios';

export type TaskResponce =
  | { uuid: string; number: number; isBlocked: boolean; blockedUUID: string }
  | {
      error: {
        code: number;
        description: string;
      };
    };

export enum TaskResultStatus {
  ready = 'ready',
  error = 'error',
  wait = 'wait',
  inProgress = 'inProgress',
  interrupted = 'interrupted',
  blocked = 'blocked',
  canceled = 'canceled',
}

export enum RequestTypes {
  openShift = 'openShift',
  closeShift = 'closeShift',
  cashIn = 'cashIn',
  cashOut = 'cashOut',
  sell = 'sell',
  sellReturn = 'sellReturn',
  buy = 'buy',
  buyReturn = 'buyReturn',
}

export type TaxationType =
  | 'osn' // 
  | 'usnIncome' //  ()
  | 'usnIncomeOutcome' //  (  )
  | 'envd' //     
  | 'esn' //   
  | 'patent'; //   

export type TaskResultResponce = {
  results: {
    error: {
      code: number;
      description: string;
    };
    status: TaskResultStatus;
  }[];
};

export type Session = {
  taxationType: TaxationType;
  operator: {
    name: string;
    vatin: string; //  
  };
};

export type PositionTax =
  | 'none' //  
  | 'vat0' //  0%
  | 'vat10' //  10%
  | 'vat110' //  10/110
  | 'vat18' //  18%
  | 'vat118' //  18/118%
  | 'vat20' //  20%
  | 'vat120'; //  20/120%

export type Item = {
  type: 'position';
  name: string; // 
  price: number; //    
  quantity: number; //  
  amount: number; //  
  infoDiscountSum?: number; //  .     ,       .
  tax: {
    type: PositionTax;
    sum?: number; //  
  };
  paymentMethod?:
    | 'fullPrepayment' //  :  100%
    | 'prepayment' // 
    | 'advance' // 
    | 'fullPayment' //  
    | 'partialPayment' //    
    | 'credit' //   
    | 'creditPayment'; //  
  paymentObject?: 'commodity' | 'excise' | 'job' | 'service'; //  : commodity
  department?: number; //  / .  : 1
  markingCode: {
    type?: 'other' | 'egais20' | 'egais30'; //  .  : other
    mark: string; // base64-  
  };
};

export type Payment = {
  type:
    | 'cash' // 
    | 'electronicaly' // 
    | 'prepaid' //   ()
    | 'credir' //   ()
    | 'other'; //    ( )
  sum: number;
};

export type MinimumArray<T> = [T, ...T[]];

export type Sell = {
  items: MinimumArray<Item>;
  payments: MinimumArray<Payment>;
  total?: number; //  .       ,
  //   .    -      
};

export type SellRequest = { type: RequestTypes } & Session & Sell;

export type AtolDriverInterface = {
  openShift: () => AxiosPromise<TaskResponce>;
  closeShift: () => AxiosPromise<TaskResponce>;
  cashIn: (sum: number) => AxiosPromise<TaskResponce>;
  cashOut: (sum: number) => AxiosPromise<TaskResponce>;
  sell: (data: Sell) => AxiosPromise<TaskResponce>;
  checkStatus: (uuid: string, callIndex?: number) => Promise<TaskResultStatus>;
};
